.login-btn{
    margin-top: 5px;
    font-weight:900;

    background-color: #105099;
    right:5px;
    width:15vw;
    position:absolute;
}
p{
    text-align:center;
}
.list-item-div{
    display: grid;
    grid-template-columns: auto 1fr auto;
   
}
.list-item-div input{
    margin: 4px 18px 4px 6px;
    transform: translate(0%,32%);

}
.list-item-div input:checked + li{
     text-decoration: line-through solid ;
     color: black;
}
.list-item-div button{
    background-color: transparent;
    padding: 0px 3px;
    margin: 4px 6px 4px 20px;
    text-align: center;
    color: #3d0b0b;
    opacity: 1;
    border-radius: 2px;
}
.list-item-div button:hover{
    background-color: hsl(266, 61%, 41%);
}
.list-item-div:hover{
    background-color: hsl(214, 97%, 45%);
    border-radius: 4px;
}
.list-item-div:hover > button{
    opacity: 1;
}