body{
  background-color: #2f89fc;
}

App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  animation: App-logo-spin infinite 10s linear;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
p{
  margin:0;
}
.App-header {
  display: grid;
  align-items: center;
  justify-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 20px;
}

.App-link {
  color: #61dafb;
}
.todo-center{
  width: 100%;
  max-width: 60vw;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr;
  text-transform: capitalize;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
input,button{
  border: none;
  padding:8px;
  font-size: calc(16px + .2vw);
}
input:focus{
  outline: none;
}
button{
  background-color: #071a52;
  color: white;
  cursor: pointer;
  font-weight: 600;
}
p{
  margin: 0px;
}
ul{
  list-style: none;
}